* {
  box-sizing: border-box;
  margin: 0;
  font-family: Arial, Helvetica Neue, Helvetica, sans-serif;
}

.people {
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  gap: 30px;
  display: grid;
}

@media (width >= 750px) {
  .people {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}

@media (width >= 1500px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}

img {
  border-radius: 200px;
  width: 200px;
  height: 200px;
  transition: border-radius 1s;
}

.person {
  flex-direction: column;
  align-items: center;
  width: 200px;
  height: 231px;
  transition: transform 1s;
  display: flex;
  position: relative;
}

.person:hover {
  z-index: 1;
  background-color: #fff;
  border-radius: 10px;
  transform: scale(1.2);
}

.person:hover img {
  border-radius: 60px;
}

.person:hover .extra {
  opacity: 1;
}

